function interceptMethod(target, methodName, interceptor) {
    const originalMethod = target[methodName];
    const newMethod = (...args) => interceptor.bind(target)(originalMethod, ...args);
    Object.assign(target, { [methodName]: newMethod });
    return () => {
        if (target[methodName] !== newMethod) {
            throw new Error(
                `Unexpected method "${methodName}" on ${target} likely due to out-of-sequence interceptor teardown.`
            );
        }
        Object.assign(target, { [methodName]: originalMethod });
        return originalMethod;
    };
}

function isResizeObserverLoopErrorMessage(message) {
    return (
        message.includes('ResizeObserver loop limit exceeded') ||
        message.includes('ResizeObserver loop completed with undelivered notifications')
    );
}

function ignoreWindowOnError(messagePredicate) {
    return interceptMethod(window, 'onerror', (originalOnError, ...args) => {
        const message = typeof args[0] === 'string' ? args[0] : args[0]?.message;
        if (messagePredicate(message)) {
            return;
        }
        return originalOnError?.apply(window, args);
    });
}

function ignoreWindowResizeObserverLoopErrors() {
    return ignoreWindowOnError(isResizeObserverLoopErrorMessage);
}

function preventResizeObserverLoopErrorEventDefaults() {
    const eventListener = (ev) => {
        if (typeof ev.message === 'string' && isResizeObserverLoopErrorMessage(ev.message)) {
            ev.preventDefault?.();
            ev.stopPropagation?.();
            ev.stopImmediatePropagation?.();
        }
    };
    const removeEventListener = () => window.removeEventListener('error', eventListener);
    window.addEventListener('error', eventListener);
    // Returns a function that can be used to remove the event listener.
    return removeEventListener;
}

let singletonPreventResizeObserverError: (() => void) | null;
export const preventResizeObserverError = (): (() => void) | null => {
    if (!singletonPreventResizeObserverError) {
        const i0 = preventResizeObserverLoopErrorEventDefaults();
        const i1 = ignoreWindowResizeObserverLoopErrors();
        singletonPreventResizeObserverError = () => {
            i0();
            i1();
            singletonPreventResizeObserverError = null;
        };
        return singletonPreventResizeObserverError;
    }
    return null;
};
